@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;600&family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body{
  width: 100%;
  margin: 0;
  padding:0;
  overflow-x: hidden; 
  
}
.App {
  text-align: center;
}
p {
  text-align: left;
}
h3 {
  margin-bottom: 5px;
  color: #ebebeb;
}
