.contact_form_container {
  background: #f5f5f5;
  margin:auto;
    min-height: 600px;
  }
.contact_text {
  margin: 30px;
  padding: 0 30px 0 30px;
}
.contact_form {
  width:300px;
  padding-top: 10px;
  display: flex;
  flex-direction: column;
  margin: auto;
}

.name_label, .email_label, .message_label {
  text-align: justify;
  padding: .5em;
  font-size: 1em;
}

.name_input, .email_input, .message_input {
  padding:10px;
  margin-bottom: 15px;
  border: 0px solid #d3d3d3;
  background:#00009910;
  border-radius: 5px;
}

.message_input {
  min-height: 90px;
}

.form_submit_button {
  margin-top: 10px;
  margin-bottom: 30px;
  display:block;
  text-decoration: none;
  padding:10px 25px;
  width: 100px;
  display: inline-block;
  border: .05em solid #acacac;
  background: #a1a1a125;
  font-size: 1em;
  border-radius: 5px;
}

a {
  text-decoration: none;
  color: #000000;
}

@media all and (min-width:550px){
  .contact_form_container {
    margin-top:100px;
    display: flex;
    justify-content: space-evenly;
  }
  .contact_text h2 {
    margin-top:0;
  }
  .contact_text {
    flex:1 1;
    max-width: 300px;
    margin: 15px auto;
  }
  .contact_form {
    flex:1 1;
    max-width: 300px;
    margin: 0 auto;
  }

  .contact_text ul{
    padding: 20px;
  }
}

.project_main_container {
  display: flex;
  justify-content: center;
  margin: auto;
  min-height: 600px;
}
.center {
  text-align: center;
  color: #5e5e5e;
}
.project_container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 300px;
  background-image: linear-gradient(to bottom, #f1f1f1 0%, #f8f8f8 100%);
  color: #000000;
  box-shadow: 1px 1px 4px #bbbbbb;
  border-radius: 15px;
  margin: 20px 15px 10px 15px;
  padding: 20px;
  align-items: stretch;
}
.project_container p {
  text-align: justify;
}
.project_main_container h3{
    color: #000000;
    font-weight: 500;
}
.project_image {
  flex: 1 1;
  width: 220px;
  height: 220px;
  margin: auto;
  /* border:1px solid #ffffff; */
  border-radius: 50%;
}
.blackjack:hover {
  box-shadow: 1px 1px 2px rgb(99, 99, 99), 0 0 10px #f7be0241, 0 0 5px #d1700241;
}
.repetition:hover {
  box-shadow: 1px 1px 2px rgb(102, 102, 102), 0 0 10px #2cdde636,
    0 0 5px #2ccaff38;
}
.battleship:hover {
  box-shadow: 1px 1px 2px rgb(0, 0, 0), 0 0 10px #80138c8c, 0 0 5px #5409917c;
}
.buttons {
  display: inline-flex;
  justify-content: center;
}
.links {
  border: 1px solid #c2c2c2;
  width: 100px;
  height: 25px;
  margin: 10px;
  padding: 5px;
  border-radius: 4px;
  box-shadow: 1px 1px 2px #dbdbdb;
}
.links:hover {
  border: 1px solid #3f3f3f;
}
.links:active {
  background: #ffffff;
}
.project_main_container ul {
  text-align: left;
  margin: 0;
  padding: 0;
  list-style-type: none;
  columns: 2;
  -webkit-columns: 2;
  -moz-columns: 2;
}
@media screen and (max-width: 550px) {
  .project_main_container {
    flex-direction: column;
  }
  .project_container {
    margin: 10px 20px 10px 20px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    align-items: center;
    max-width: none;
  }
}

.aboutme_section {
    background: #f5f5f5;
    min-height: 600px;
    display: flex;
    flex-direction: column;
}
.aboutme_section h2{
    text-align: center;
}
.aboutme_content_container{
    display: flex;
    flex-direction: column;
}
.aboutme_image {
    width: 200px;
    height: 200px;
    margin: 50px;
    border: 5px solid #ffffff;
    border-radius: 50%;
}

.aboutme_text {
    width: 300px;
    margin:auto;
    padding:0 20px 30px 20px;

}

@media all and (min-width:550px){
    .aboutme_section {
        display: flex;
        justify-content: space-evenly;
        min-height:500px;
    }
    .aboutme_image_container {
        flex: 1 1;
        margin: auto;
        max-width: -webkit-fit-content;
        max-width: -moz-fit-content;
        max-width: fit-content;
    }
    .aboutme_text {
        flex: 1 1;
        margin:auto;
        max-width: 400px;
    }
    .aboutme_content_container{
        flex-direction: row;
    }
}

.homepage_container {
    color: #ffffff;
    text-shadow: 1px 1px 2px #555555;
    display: flex;
    width: inherit;
    flex-direction: column;
    margin: auto;
    min-height: 600px;
}

.homepage_container{
    background-image:linear-gradient(to bottom, #000099c0 0%, #33ccffc0 100%),
    url(/static/media/technology.3312b9a3.jpg); 
    background-size: cover;
    background-position: center;
    
}

h1{
    font-size: 45px;
    margin:2px;
}
.homepage_container h2 {
    font-size: 22px;
}

.homepage_container span{
    margin: auto;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

@media all and (min-width:550px){
    h1 {
        font-size: 60px;
        
    }
}


.skills_container {
    display: flex;
    flex-direction: column;
    margin: auto;
    min-height: 600px;
}

.skills_image{
  height: 75px;
  width: 75px;
}
.learned_skills{
  margin: 0;
  padding: 0;
  /* margin: auto; */
  justify-content: center;
  list-style-type: none;
  columns: 2;
  -webkit-columns: 2;
  -moz-columns: 2;
}
.skills_container{
  align-items: center;
}
.learned_skills li{
  display: flex;
  width: 250px;
  padding: 10px;
  margin: 0 20px 0 20px;
  
}
.learned_skills li div{
  margin: auto;
  font-size: 25px;
  font-weight: 800;
}
.loading {
    margin: auto;
}

  .loading span {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 10px;
    background: #007DB6;
    border-radius: 50%;
    -webkit-animation: loading 1s infinite alternate;
            animation: loading 1s infinite alternate;
  }

  .loading span:nth-of-type(2) {
    background: #008FB2;
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
  }
  .loading span:nth-of-type(3) {
    background: #009B9E;
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
  }
  .loading span:nth-of-type(4) {
    background: #00A77D;
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
  }
  .loading span:nth-of-type(5) {
    background: #00B247;
    -webkit-animation-delay: 0.8s;
            animation-delay: 0.8s;
  }
  .loading span:nth-of-type(6) {
    background: #5AB027;
    -webkit-animation-delay: 1.0s;
            animation-delay: 1.0s;
  }
  .loading span:nth-of-type(7) {
    background: #A0B61E;
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
  }
  @-webkit-keyframes loading {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes loading {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  @media all and (max-width:500px) {
    .learned_skills{
      display: flex;
      flex-direction: column;
      margin: auto;
    }
  }
